import { StoryPointModel } from '@tableau/api-internal-contract-js';
import { ApiService } from './ServiceRegistry';

/**
 * Service for implementing the logic for story activation
 *
 * @interface StoryActivationService
 * @extends {ApiService}
 */
export interface StoryActivationService extends ApiService {
  /**
   * Activates the next story point
   *
   * @return  This function does not return anything.
   */
  activateNextStoryPointAsync(): Promise<void>;

  /**
   * Activates the previous story point
   *
   * @return  This function does not return anything.
   */
  activatePreviousStoryPointAsync(): Promise<void>;

  /**
   * Activates the story point at the specified index
   *
   * @param {index} index  The visual to apply filter
   * @return  This function does not return anything.
   */
  activateStoryPointAsync(index: number): Promise<void>;

  /**
   * Reverts the story point at the specified index
   *
   * @param {index} index  The visual to apply filter
   * @return  The story point model that was reverted
   */
  revertStoryPointAsync(index: number): Promise<StoryPointModel>;
}
